import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useScrollLock } from '@/hooks/useScrollLock';
import { SITE } from '@/config/site';

export default function Navbar() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  // Note: location was unused; removed to satisfy TS noUnusedLocals

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  useScrollLock(isMenuOpen);

  const handleLinkClick = () => {
    setIsMenuOpen(false);
  };

  return (
    <>
      <nav
        className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
          isScrolled ? 'bg-surface-2/95 backdrop-blur-md shadow-sm' : 'bg-transparent'
        }`}
      >
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <Link to="/" className="flex items-center space-x-2">
              <div className={`w-10 h-10 flex items-center justify-center ${isScrolled ? 'text-primary' : 'text-white'}`}>
                <i className="ri-home-heart-fill text-3xl"></i>
              </div>
              <span className={`text-2xl font-bold font-serif ${isScrolled ? 'text-gray-900' : 'text-white'}`}>
                MARUTAKE
              </span>
            </Link>

            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className={`flex items-center gap-2 px-6 py-2.5 text-sm font-medium rounded-full transition-colors whitespace-nowrap cursor-pointer ${
                isScrolled
                  ? 'bg-surface-1 text-white hover:bg-surface-1'
                  : 'bg-surface-2/10 backdrop-blur-md border border-surface-2/30 text-white hover:bg-surface-2/20'
              }`}
            >
              {isMenuOpen ? (
                <>
                  <i className="ri-close-line text-lg"></i>
                  <span>CLOSE</span>
                </>
              ) : (
                <>
                  <i className="ri-menu-line text-lg"></i>
                  <span>MENU</span>
                </>
              )}
            </button>
          </div>
        </div>
      </nav>

      {/* Full Screen Menu Overlay */}
      <div
        className={`fixed inset-0 bg-surface-1 z-[100] transition-all duration-500 ${
          isMenuOpen ? 'opacity-100 visible' : 'opacity-0 invisible'
        }`}
      >
        {/* Close Button */}
        <div className="absolute top-0 left-0 right-0 z-[110]">
          <div className="max-w-7xl mx-auto px-6 py-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-2">
                <div className="w-10 h-10 flex items-center justify-center text-white">
                  <i className="ri-home-heart-fill text-3xl"></i>
                </div>
                <span className="text-2xl font-bold font-serif text-white">
                  MARUTAKE
                </span>
              </div>

              <button
                onClick={() => setIsMenuOpen(false)}
                className="flex items-center gap-2 px-6 py-2.5 text-sm font-medium rounded-full transition-colors whitespace-nowrap cursor-pointer bg-surface-2/10 backdrop-blur-md border border-surface-2/30 text-white hover:bg-surface-2/20"
              >
                <i className="ri-close-line text-lg"></i>
                <span>CLOSE</span>
              </button>
            </div>
          </div>
        </div>

        <div className="h-full flex flex-col items-center justify-center px-6">
          <div className="w-full max-w-5xl">
            <div className="grid grid-cols-5 gap-8 items-stretch">
              {/* Col 1: ホーム */}
              <div
                className="flex flex-col items-center justify-center border-r border-surface-2/30 last:border-r-0"
                style={{ opacity: isMenuOpen ? 1 : 0, transform: isMenuOpen ? 'translateY(0)' : 'translateY(20px)', transition: 'all 0.5s ease 0.00s' }}
              >
                <Link to="/" onClick={handleLinkClick} className="text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                  ホーム
                </Link>
              </div>

              {/* Col 2: 1行：コンセプト 2行：MARUTAKEができるまで */}
              <div
                className="flex flex-col items-center justify-center border-r border-surface-2/30 last:border-r-0"
                style={{ opacity: isMenuOpen ? 1 : 0, transform: isMenuOpen ? 'translateY(0)' : 'translateY(20px)', transition: 'all 0.5s ease 0.05s' }}
              >
                <div className="flex flex-col items-center text-center divide-y divide-surface-2/30">
                  <Link to="/concept" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    コンセプト
                  </Link>
                  <Link to="/story" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    MARUTAKEができるまで
                  </Link>
                </div>
              </div>

              {/* Col 3: 1行：シェアハウスについて 2行：部屋・共用スペース */}
              <div
                className="flex flex-col items-center justify-center border-r border-surface-2/30 last:border-r-0"
                style={{ opacity: isMenuOpen ? 1 : 0, transform: isMenuOpen ? 'translateY(0)' : 'translateY(20px)', transition: 'all 0.5s ease 0.10s' }}
              >
                <div className="flex flex-col items-center text-center divide-y divide-surface-2/30">
                  <Link to="/sharehouse" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    シェアハウスについて
                  </Link>
                  <Link to="/rooms" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    部屋・共用スペース
                  </Link>
                </div>
              </div>

              {/* Col 4: 1行：アクセス 2行：Q&A 3行：ブログ */}
              <div
                className="flex flex-col items-center justify-center border-r border-surface-2/30 last:border-r-0"
                style={{ opacity: isMenuOpen ? 1 : 0, transform: isMenuOpen ? 'translateY(0)' : 'translateY(20px)', transition: 'all 0.5s ease 0.15s' }}
              >
                <div className="flex flex-col items-center text-center divide-y divide-surface-2/30">
                  <Link to="/access" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    アクセス
                  </Link>
                  <Link to="/faq" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    Q&amp;A
                  </Link>
                  <Link to="/blog" onClick={handleLinkClick} className="py-2 text-2xl font-medium text-white hover:text-primary transition-colors cursor-pointer">
                    ブログ
                  </Link>
                </div>
              </div>

              {/* Col 5: 見学予約 */}
              <div
                className="flex flex-col items-center justify-center"
                style={{ opacity: isMenuOpen ? 1 : 0, transform: isMenuOpen ? 'translateY(0)' : 'translateY(20px)', transition: 'all 0.5s ease 0.20s' }}
              >
                <a
                  href={SITE.social.line}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline-block px-10 py-4 bg-primary text-white text-lg font-medium rounded-full hover:bg-accent transition-all shadow-lg whitespace-nowrap cursor-pointer"
                >
                  見学予約
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
